import Label from "./label";
import Typography from "./typography";

const noopFormat = v => v

export default class Slider extends React.PureComponent {
  render() {
    const {
      label,
      format = noopFormat,
      value
    } = this.props
    return (
      <div>
        <Label>
          {label}
          <Typography.Small>{format(value)}</Typography.Small>
        </Label>
      </div>
    )
  }
}